.favorite {
  padding: 40px;

  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: #ABABAB;
    margin-bottom: 20px;
  }
}

.content-favourite {
  max-width: 400px;
  margin-bottom: 20px;
  padding: 20px;
  background: #FFFFFF;
  border: 1px solid #F8F8F8;
  box-sizing: border-box;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;

  &__addToFavorite-wrapper {
    text-align: right;
  }

  &__addToFavorite {
    border: none;
    background: #F8F8F8;
    margin-bottom: 10px;
    cursor: pointer;
  }

  &__item-container {
    display: flex;
  }

  &__textIcon-container {
    position: relative;
    width: 40px;
    height: 40px;
    margin-right: 10px;
    background: #fff;
    border-radius: 50%;
  }

  &__textIcon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

